.game .container {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 600px;
  background-color: rgba(128, 128, 128, 0.8);
  color: white;
  border: 1px solid white;
  border-radius: 20px;
  box-shadow: 4px 4px 2px white;
}
.game .container .game-title {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 300px;
}
.game .container .game-title .game-title1 {
  margin-right: auto;
  margin-block-end: 0px;
}
.game .container .game-title .game-title__attempts {
  position: absolute;
  left: 45%;
  top: -40%;
  width: 30px;
  height: 20px;
  border: 0.5px solid white;
  border-radius: 5px;
  background-color: black;
}
.game .container .game-static .game-static__lvl,
.game .container .game-static .game-static__compleat {
  position: absolute;
  left: 80%;
  top: 15%;
  margin-block-end: 0px;
  font-size: 16px;
  text-decoration: underline;
}
.game .container .game-static .game-static__compleat {
  top: 25%;
}/*# sourceMappingURL=game.css.map */