.letterPanel {
  display: flex;
}
.letterPanel .letters-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-inline-start: 70px;
  -moz-column-gap: 3px;
       column-gap: 3px;
  row-gap: 3px;
  width: 400px;
}
.letterPanel .letters-list .letters-list__item,
.letterPanel .letters-list .letters-list__win,
.letterPanel .letters-list .letters-list__present,
.letterPanel .letters-list .letters-list__miss {
  text-align: center;
  padding-bottom: 10px;
  width: 30px;
  height: 30px;
  border: 0.5px solid white;
  border-radius: 10px;
  font-size: 30px;
  cursor: pointer;
}
.letterPanel .letters-list .letters-list__item:hover,
.letterPanel .letters-list .letters-list__win:hover,
.letterPanel .letters-list .letters-list__present:hover,
.letterPanel .letters-list .letters-list__miss:hover {
  opacity: 0.8;
}
.letterPanel .letters-list .letters-list__item:active,
.letterPanel .letters-list .letters-list__win:active,
.letterPanel .letters-list .letters-list__present:active,
.letterPanel .letters-list .letters-list__miss:active {
  transform: scale(0.9);
}
.letterPanel .letters-list .letters-list__item {
  color: white;
  background-color: black;
}
.letterPanel .letters-list .letters-list__win {
  color: black;
  background-color: yellow;
  animation: swapWin 1s linear;
}
.letterPanel .letters-list .letters-list__present {
  border: 0.5px solid black;
  color: black;
  background-color: white;
  animation: swapPresent 1s linear;
}
.letterPanel .letters-list .letters-list__miss {
  color: white;
  background-color: rgba(128, 128, 128, 0.6);
  animation: swapMiss 1s linear;
}
.letterPanel .symbols-list {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  row-gap: 10px;
  -moz-column-gap: 5px;
       column-gap: 5px;
  padding-inline-start: 0;
}
.letterPanel .symbols-list .symbols-list__item-delete,
.letterPanel .symbols-list .symbols-list__item-check,
.letterPanel .symbols-list .symbol-list__item-restartGame,
.letterPanel .symbols-list .symbol-list__item-restartLvl,
.letterPanel .symbols-list .symbol-list__item-next,
.letterPanel .symbols-list .symbol-list__item-last {
  width: 35px;
  height: 26px;
  border: 0.5px solid black;
  border-radius: 10px;
  background-color: white;
  cursor: pointer;
}
.letterPanel .symbols-list .symbols-list__item-delete {
  margin-top: auto;
}
.letterPanel .symbols-list .symbols-list__item-check {
  background-color: yellow;
}
.letterPanel .symbols-list .symbol-list__item-restartGame {
  position: absolute;
  top: 75%;
  left: -1350%;
  width: 50px;
  color: black;
  font-size: 12px;
  font-weight: 500;
}
.letterPanel .symbols-list .symbol-list__item-next {
  position: absolute;
  top: -35%;
  left: 50%;
}
.letterPanel .symbols-list .symbol-list__item-last {
  position: absolute;
  top: -35%;
  left: -90%;
}
.letterPanel .symbols-list .symbol-list__item-restartLvl {
  position: absolute;
  top: -250%;
  left: -70%;
  font-size: 12px;
  width: 50px;
  color: black;
  font-weight: 500;
}
.letterPanel .symbols-list .symbols-list__item-delete:hover,
.letterPanel .symbols-list .symbols-list__item-check:hover,
.letterPanel .symbols-list .symbol-list__item-restartGame:hover,
.letterPanel .symbols-list .symbol-list__item-restartLvl:hover,
.letterPanel .symbols-list .symbol-list__item-next:hover,
.letterPanel .symbols-list .symbol-list__item-last:hover {
  opacity: 0.8;
}
.letterPanel .symbols-list .symbols-list__item-delete:active,
.letterPanel .symbols-list .symbols-list__item-check:active,
.letterPanel .symbols-list .symbol-list__item-restartGame:active,
.letterPanel .symbols-list .symbol-list__item-restartLvl:active,
.letterPanel .symbols-list .symbol-list__item-next:active,
.letterPanel .symbols-list .symbol-list__item-last:active {
  transform: scale(0.9);
}

@keyframes swapWin {
  25% {
    transform: scale(0.5);
    background-color: black;
    color: white;
  }
  50% {
    transform: scale(0);
  }
  75% {
    transform: scale(0.5);
    background-color: yellow;
    color: black;
  }
  100% {
    transform: scale(1);
  }
}
@keyframes swapMiss {
  25% {
    transform: scale(0.5);
    background-color: black;
    color: white;
  }
  50% {
    transform: scale(0);
  }
  75% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes swapPresent {
  25% {
    transform: scale(0.5);
    background-color: black;
    color: white;
  }
  50% {
    transform: scale(0);
  }
  75% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}/*# sourceMappingURL=letterPanel.css.map */