.letterPanel {
    display: flex;
    .letters-list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding-inline-start: 70px;
        column-gap: 3px;
        row-gap: 3px;
        width: 400px;
        
        .letters-list__item,
        .letters-list__win,
        .letters-list__present,
        .letters-list__miss {
            text-align: center;
            padding-bottom: 10px;
            width: 30px;
            height: 30px;
            border: .5px solid white; 
            border-radius: 10px;
            font-size: 30px;
            cursor: pointer;
        }

        .letters-list__item:hover,
        .letters-list__win:hover,
        .letters-list__present:hover,
        .letters-list__miss:hover {
            opacity: .8;
        }

        .letters-list__item:active,
        .letters-list__win:active,
        .letters-list__present:active,
        .letters-list__miss:active {
            transform: scale(.9);
        }

        .letters-list__item {
            color: white;
            background-color: black;
        }

        .letters-list__win {
            color: black;
            background-color: yellow;
            animation: swapWin 1s linear;
        }

        .letters-list__present {
            border: .5px solid black;
            color: black;
            background-color: white;
            animation: swapPresent 1s linear;
        }

        .letters-list__miss {
            color: white;
            background-color: rgba($color: grey, $alpha: .6);
            animation: swapMiss 1s linear;
        }
    }
    .symbols-list {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        row-gap: 10px;
        column-gap: 5px;
        padding-inline-start: 0;
        .symbols-list__item-delete,
        .symbols-list__item-check,
        .symbol-list__item-restartGame,
        .symbol-list__item-restartLvl,
        .symbol-list__item-next,
        .symbol-list__item-last {
            width: 35px;
            height: 26px;
            border: .5px solid black;
            border-radius: 10px;
            background-color: white;
            cursor: pointer;
        }

        .symbols-list__item-delete {
            margin-top: auto;
        }

        .symbols-list__item-check {
            background-color: yellow;
        }

        .symbol-list__item-restartGame {
            position: absolute;
            top: 75%;
            left: -1350%;
            width: 50px;
            color: black;
            font-size: 12px;
            font-weight: 500;
        }

        .symbol-list__item-next {
            position: absolute;
            top: -35%;
            left: 50%;
        }

        .symbol-list__item-last {
            position: absolute;
            top: -35%;
            left: -90%;
        }

        .symbol-list__item-restartLvl {
            position: absolute;
            top: -250%;
            left: -70%;
            font-size: 12px;
            width: 50px;
            color:  black;
            font-weight: 500;
        }

        .symbols-list__item-delete:hover,
        .symbols-list__item-check:hover,
        .symbol-list__item-restartGame:hover,
        .symbol-list__item-restartLvl:hover,
        .symbol-list__item-next:hover,
        .symbol-list__item-last:hover  {
            opacity: .8;
        }

        .symbols-list__item-delete:active,
        .symbols-list__item-check:active,
        .symbol-list__item-restartGame:active,
        .symbol-list__item-restartLvl:active,
        .symbol-list__item-next:active,
        .symbol-list__item-last:active {
            transform: scale(.9);
        }
    }
}

@keyframes swapWin {

    25% {
        transform:scale(.5);
        background-color: black;
        color: white;
    }

    50% {
        transform: scale(0);
    }

    75% {
        transform: scale(.5);
        background-color: yellow;
        color: black;
    }

    100% {
        transform: scale(1);
    }
}

@keyframes swapMiss {

    25% {
        transform:scale(.5);
        background-color: black;
        color: white;
    }

    50% {
        transform: scale(0);
    }

    75% {
        transform: scale(.5);
        
    }

    100% {
        transform: scale(1);
    }
}

@keyframes swapPresent {

    25% {
        transform:scale(.5);
        background-color: black;
        color: white;
    }

    50% {
        transform: scale(0);
    }

    75% {
        transform: scale(.5);
        
    }

    100% {
        transform: scale(1);
    }
}