.gameField {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 800px;
    row-gap: 5px;
    .gameFieldRow {
        display: flex;
        margin: 3px 0 3px;
        padding-inline-start: 0;
        margin-left: 0;
        column-gap: 10px;
        .gameFieldColumnDefault,
        .gameFieldColumnWin,
        .gameFieldColumnMiss,
        .gameFieldColumnPresent {
            width: 50px;
            height: 50px;            
            border-radius: 10px;
            font-size: 42px;
        }

        .gameFieldColumnDefault {
            border: 1px solid yellow;
            background-color: black;
            color:  white;
        }

        .gameFieldColumnWin {
            border: 1px solid black;
            color: black;
            background-color: yellow;
            animation: swapWin 1s linear;
        }

        .gameFieldColumnMiss {
            border: .5px solid black;
            color: white;
            background-color: rgba($color: grey, $alpha: .6);
            animation: swapMiss 1s linear;
        }

        .gameFieldColumnPresent {
            border: 1px solid black;
            color: black;
            background-color: white;
            animation: swapPresent 1s linear;
        }
    }
}

@keyframes swapWin {

    25% {
        transform:scale(.5);
        background-color: black;
        color: white;
    }

    50% {
        transform: scale(0);
    }

    75% {
        transform: scale(.5);
        background-color: yellow;
        color: black;
    }

    100% {
        transform: scale(1);
    }
}

@keyframes swapMiss {

    25% {
        transform:scale(.5);
        background-color: black;
        color: white;
    }

    50% {
        transform: scale(0);
    }

    75% {
        transform: scale(.5);
        
    }

    100% {
        transform: scale(1);
    }
}

@keyframes swapPresent {

    25% {
        transform:scale(.5);
        background-color: black;
        color: white;
    }

    50% {
        transform: scale(0);
    }

    75% {
        transform: scale(.5);
        
    }

    100% {
        transform: scale(1);
    }
}